export const nameFirstLetters = (name) => {
  const splitName = name.split(' ').map(val => val[0])
  return splitName.length > 2 ? splitName[0] + splitName[splitName.length - 1] : splitName[0]
}

export const getAddress = (addr1, addr2) => {
  // return `${(`${addr1   }, `) || ''}  ${(`${addr2  }, `) || ''} ${(`${city  }, `) || ''} ${(`${state  }, `) || ''} ${(`${country  }, `) || ''} ${zip || ''}`
  // return `${(`${addr1 } `) || ''}  ${(`${addr2} `) || ''}`
  return `${(`${!addr1 || null ? "-" : addr1} `)} ${(`${!addr2 || null ? "-" : addr2} `)}`
}
