import React, { Fragment } from 'react'
import DataTable from 'react-data-table-component'
import { Card, Row, Col, Button } from 'reactstrap'
import '@styles/react/libs/tables/react-dataTable-component.scss'
// import {GetTransaction} from "../store/actions"
import { useHistory, useLocation } from 'react-router-dom'
import {useSelector } from 'react-redux'
// import { getAllUsers } from '../store/actions'

const ProfileTable = ({ columns, data, title }) => {
  const store = useSelector((state) => state.users)
  const history = useHistory()
  // const params = useParams()
  // const dispatch = useDispatch()
  const location = useLocation()
  console.log(location.pathname)
  console.log(store)
  console.log(store.user_per_page.last_page)

  const next = () => {   
    console.log(store)
      // dispatch(GetTransaction(store.userDetails[1].u_id)) 
    history.push(`/users/${store.userDetails[1].u_id}/${title.replace(" ", "")}`)
  }
  const HeaderRender = () => (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-2">
      <Row>
        <Col xl="6" className="d-flex align-items-center p-0">
          <h3 className="mb-0">{title}</h3>
        </Col>
        <Col
          xl="6"
          className="d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
        >
          {data?.length > 0 && data?.length >= 5 && (
            <Button
            color='primary'
            onClick={next}
              className="waves-effect btn btn-primary"
              // href="/react-admin/apps/user/edit/6"
            >
              View All
            </Button>
          )}
        </Col>
      </Row>
    </div>
  )
  return (
    <Fragment>
      <Card>
        <DataTable
          noHeader
          subHeader
          columns={columns}
          className="react-dataTable"
          data={data}
          subHeaderComponent={<HeaderRender />}
        />
      </Card>
    </Fragment>
  )
}

export default ProfileTable
